
import { computed, defineComponent, onMounted, ref} from "vue";
import Loading from "@/components/Loading.vue";
import SceneCardItem from "@/components/card/SceneCardItem.vue";
import HotelCardItem from "@/components/card/HotelCardItem.vue";
import ActivityCardItem from "@/components/card/ActivityCardItem.vue";
import FoodCardItem from "@/components/card/FoodCardItem.vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
//1.可以隨機亂骰
//2.也可以選擇城市來骰
export default defineComponent({
  components: {
    Loading,
    SceneCardItem,
    HotelCardItem,
    ActivityCardItem,
    FoodCardItem,
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    const loading = ref(false);
    const defaultPicture = require("../assets/images/defaultImg.webp");
    const recommendData = computed(() => {
      loading.value = false; //剛開始時不需要讓人家看到loading
      return store.state.RandomItem.randomItemList[0];
    });
    function randomRecommend() {
      loading.value = true;
      store.dispatch("RandomItem/getRandomItem");
    }
    function gotItemPage(data: any) {
      let temp = JSON.stringify(data);
      router.push({
        path: `/ItemDisplay`,
        query: {
          data: temp,
        },
      });
    }
    return {
      //data
      recommendData,
      defaultPicture,
      loading,
      //methods
      randomRecommend,
      gotItemPage,
    };
  },
});
